.App {
  text-align: center;
}

.App-logo {
  /* animation: App-logo-spin infinite 20s linear; */
  height: 50px;
}

.App-header {
  background-color: #e0e0e0;
  height: 150px;
  padding: 20px;
  color: #333;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.content {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:row;
      flex-direction:row;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
}

.frame1 {
  border:1px solid #7b72a9;
  width:600px;
  height:600px;
  position:relative;
}

.frame1 .grid {
  position:absolute;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  width:100%;
  height:100%;
  top:0;
  left:0;
  z-index:10;
}

.grid.grid-hor > div, .grid.grid-ver > div {
  border-color:#a0a0a0;
  border-style:solid;
  border-width:0;
  border-left-width:1px;
  border-top-width:1px;
  -ms-flex:1 0;
      flex:1 0;
  height:auto;
}

.grid.grid-hor {
  -ms-flex-direction:column;
      flex-direction:column;
}

.grid.grid-ver {
  -ms-flex-direction:row;
      flex-direction:row;
}

.arrow {
  border-radius:130px;
  width:20px;
  height:20px;
  position:absolute;
  z-index:100;
  -webkit-transition-timing-function: linear;
       -o-transition-timing-function: linear;
          transition-timing-function: linear;
  -webkit-transition-property: top, left, -webkit-transform;
  transition-property: top, left, -webkit-transform;
  -o-transition-property: top, left, transform;
  transition-property: top, left, transform;
  transition-property: top, left, transform, -webkit-transform;
  -webkit-transition-duration:.5s;
       -o-transition-duration:.5s;
          transition-duration:.5s;
}

.controls {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:column;
      flex-direction:column;
  -ms-flex-pack:center;
      justify-content:center;
  width:100px;
  min-height:100px;
}

.blink {
  -webkit-animation-duration: .8s;
          animation-duration: .8s;
  -webkit-animation-name: blink1;
          animation-name: blink1;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}

.blink2 {
  -webkit-animation-duration: .4s;
          animation-duration: .4s;
  -webkit-animation-name: blink1;
          animation-name: blink1;
  -webkit-animation-iteration-count: 2;
          animation-iteration-count: 2;
}

.teleport {
  -webkit-animation:teleport 1s linear;
          animation:teleport 1s linear;
}
.teleport-middle {
  /* opacity:0; */
  /* transition: opacity .5s linear; */
  opacity: 0;
}
.teleport-end {
  -webkit-animation:teleport-final .5s linear;
          animation:teleport-final .5s linear;
}

@-webkit-keyframes blink1 {
  0% {
    background:transparent;
  }
  50% {
    background:red;
  }
  100% {
    background:transparent;
  }
}

@keyframes blink1 {
  0% {
    background:transparent;
  }
  50% {
    background:red;
  }
  100% {
    background:transparent;
  }
}

@-webkit-keyframes teleport {
  0% {
    background:#9933ff;
  }
  100% {
    opacity:0;
  }
}

@keyframes teleport {
  0% {
    background:#9933ff;
  }
  100% {
    opacity:0;
  }
}
@-webkit-keyframes teleport-final {
  0% {
    opacity: 0;
  }
  100% {
    opacity:1;
  }
}
@keyframes teleport-final {
  0% {
    opacity: 0;
  }
  100% {
    opacity:1;
  }
}




